// Bootstrap overrides for this template
.btn {
  font-size: 14px;
  font-weight: 800;
  padding: 15px 25px;
  letter-spacing: 1px;
  text-transform: uppercase;
  border-radius: 0;
  @include sans-serif-font;
}

.btn-primary {
  background-color: $primary;
  border-color: $primary;
  &:hover,
  &:focus,
  &:active {
    color: $white;
    background-color: darken($primary, 7.5) !important;
    border-color: darken($primary, 7.5) !important;
  }
}

.btn-lg {
  font-size: 16px;
  padding: 25px 35px;
}

header.masthead .site-heading {
  h1{
    font-size: 40px;
  }

  .subheading{
    font-size: 22px;
    font-weight: 400;
    line-height: 1.5em;
    color: white;
  }

  .btn-primary {
    font-size: 20px;
  }
}

.nav-tabs .nav-item.show .nav-link, .nav-tabs .nav-link.active {
  border-color: $gray-700 $gray-700 $white;
}


.nav-tabs{
  border-bottom: 1px solid $gray-700;

  .nav-link:hover {
    border-top-color: $gray-700;
    border-left-color: $gray-700;
    border-right-color: $gray-700;
    border-bottom-color: transparent;
  }
}

.card {
  border-color: $gray-700;

  .list-group-item {
    border-color: $gray-700;
  }
}

header.masthead {

  .overlay {

    background-color: #1d2124;
    opacity: 0.7;
  }

  .site-heading, .subheading {
    text-align: left;
  }

}

.homebigbtn {
  img {
    max-width: 25%;
    height: auto;
    padding-top: 10px;
  }
}

.iframe{
  width: 100%;
}

a, p, .card {
  font-family: sans-serif;
}

h2{
  font-size: 1.7rem;
}

h3{
  font-size: 1.5rem;
}

h4{
  font-size: 1.3rem;
}

body {
  font-family: sans-serif;
}

.quote {
  max-width: 65px;
  vertical-align: bottom;
  fill: white;
}

.meta {
  background-color : #00657b;
  color: white;
  padding: 3px;
}

.btn-modal {
  background-color: $primary;
  color: white;
  padding: 5px;
  font-size: 14px;
  -webkit-border-radius:10px;
  .btn:hover{
    background-color: #00657b;
    color: white;
  }
}

.lien {
  text-decoration: underline;
}

.temps {
  font-size: 18px;
  color : $gray-600;
}